.posts{
    display: flex;
    flex-direction: column;
    gap: 10px;

}
.header img{
    height: 40px;
    width: 40px;
    border-radius: 50%;

}
.post{
    margin: 30px 100px;
    height: 700px;
    width: 450px;
    justify-content: center;
    

}
.header{
    display: flex;
    justify-items: center;
    justify-content:space-between;
    padding-top: 10px;
    padding-right: 5px;
}
.pro>span{
    font-size: 20px;
    
}
.pro{
    display: flex;
    justify-items: center;
    gap: 5px;
    margin-left: 5px;
}
.body{
    display: flex;
    justify-content: center;
    margin-top: 10px;
}
.body img{
    height: 550px;
    border-radius: 5px;
}
.footer{
    margin-top: 10px;
}
.reactions{
    display: flex;
    justify-content: space-between;
}
.icons{
    display: flex;
    gap: 15px;
}
.com input{
    width: 100%;
    outline: none;
    border: none;
    height: 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.name a{
    text-decoration: none;
}