*{
    padding: 0;
    margin: 0;
}
.nav{
    position: fixed;
    display: flex;
    flex-direction: column;
    z-index: 1;
    width: 20%;
    height: 100%;
    border-right: 1px solid grey;
}

.logo img{
    cursor: pointer;
    width: 125px;
    margin-left: 20px;
    margin-top: 20px;

}
.nav_btns{
    display: flex;
    flex-direction: column;
    margin-right: 20px;
}
.nav_btn{
   display: flex;
   flex-direction: row;
   background: 0;
   border: none;
   border-radius: 15px;
   margin: 5px 10px 5px 10px;
   padding: 10px 15px 10px 15px;
   width: 100%;
   align-items: center;

}
.nav_btn:hover{
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.128);
    font-weight: bolder;
    
}
.nav_btn>span{
    font-size: 17px;
    margin-left: 10px;
}
.nav_menu{
    position: fixed;
    border-radius: 15px;
    margin: 5px 10px 5px 10px;
    padding: 10px 15px 10px 15px;
    bottom: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    background: 0;
    border: none;
    width: 18.5%;
}
.nav_menu:hover{
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.128);
    font-weight: bolder;

    
}
.nav_menu>span{
    margin-left: 10px;
    font-size: 17px;
}