.sugg{
    /* background-color: grey; */
    margin-right: 10px;
}

.pro_avatar{
    height: 60px;
    width: 60px;
    border: 2px solid green;
}
.user_name{
    display: flex;
    flex-direction: column;
    font-weight: bolder;
}
.user_name>span{
    font-weight: normal;
    color: grey;
}
.main_user{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 15px;
}
.profile{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.profile>span{
    color: #00a2ff;
    cursor: pointer;
}
.sugg_people{
    margin-top: 10px;
}
.sugg_header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.sugg_header>span{
    font-weight: bolder;
}
.pple{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.people{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.pple>span{
    color:#00a2ff ;
}
.p_avatar img{
    height: 50px;
}