.all_story{
    display: flex;
    flex-direction: row;
    gap: 20px;
}
.story{
    display: flex;
    flex-direction: column ;
    justify-content: center;
    align-items: center;
    height: 66px;
    width: 66px;
}

.story img{
    height: 60px;
    width: 60px;
    border-radius: 50%;
    border: 2px solid rgb(8, 253, 0);
}
.story >span{
    font-size: 13px;

}
.story>span{
    font-weight: bold;
}